<template>
    <div class="logBox">
        <ul>
            <h4>用户登录</h4>
            <li>
                <label for="userName">用户名</label>
                <input name="userName" type="text" v-model="userName" placeholder="请输入用户名">
            </li>
            <li>
                <label for="password">密码</label>
                <input name="password" type="password" v-model="password"  @keyup.enter="login" placeholder="请输入密码">
            </li>
            <li style="padding:20px">
                <button type="button" class="promarry" @click="login">登录</button>
                <button type="button" @click="regsiter">注册</button>
            </li>
        </ul>
    </div>
</template>

<script>
import APiHelper from "../../libs/helpers/ApiHelper";

export default {
  data() {
    return {
      userName: "",
      password: ""
    };
  },
  methods: {
    login() {
      APiHelper.login({
        userName: this.userName,
        password: this.password
      })
        .then(res => {
        //   console.log(res);
          if (res.returnCode === "000000") {
            this.Storage.sessionSet('LOGINSHOW',true)
            this.Storage.sessionSet('USERINFO',JSON.stringify(res.data))
            window.loginShow = true;
            this.$router.push({
              path: "/waterList",
              query: {
                userName: this.userName
              }
            });
          }
        })
        .catch(err => {
          this.Toast(err.returnMsg)
        });
    },
    regsiter() {
      const path = location.origin + "/register";
    //   console.log(path);
      location.href = path;
    }
  },
  mounted() {}
};
</script>




